<template>
  <div class="layout">
    <div class="layout-nav">
      <div class="navbar">
        <logo />
        <div class="nav">
          <router-link :class="active === 1? 'active':''" to="/">主页</router-link>
          <router-link :class="active === 2? 'active':''" to="/news">公司及新闻</router-link>
          <router-link :class="active === 3? 'active':''" to="/portfolio">业绩</router-link>
          <router-link :class="active === 4? 'active':''" to="/member">团队</router-link>
          <router-link :class="active === 5? 'active':''" to="/aboute">联络</router-link>
          <div class="clean"></div>
        </div>
      </div>
    </div>
    <div class="layout-content">
      <transition class='slide-ul' name="indexlist">
        <slot></slot>
      </transition>
    </div>
  </div>
</template>
<script>
  import logo from './logo.vue'

  export default {
    components:{
      logo
    },
    props:['active']
  }
</script>

<style lang="less" scoped>
  /*Vue transition*/
  .outLeftInRight-transition,
  .fadeOutLeft-transition, 
  .fadeInLeft-transition {
    transition: transform .3s ease-in-out;
  }
  /*进入时的状态，从右开始*/
  .outLeftInRight-enter,
  .fadeInLeft-enter,
  .fadeInLeft-leave{
    transform: translateX(100%);
  }
  /*离开时的状态，从左开始*/
  .outLeftInRight-leave,
  .fadeOutLeft-enter,
  .fadeOutLeft-leave{
    transform: translateX(-100%);
  }
</style>



