<template>
  <div class="newslist">
    <img :src="database.imgurl">
    <div class="con">
      <div class="tits" v-html="database.title"></div>
      <div class="text" :style="newStyle" v-html="database.content"></div>
      <div class="more" @click="showContent"><b>{{btnstr}}</b></div>
      <div class="time" v-html="database.time"></div>
    </div>
    <div class="fix"></div>
  </div>
</template>
<script>
  export default {
    data(){
      return {
        newConHeight:80,
        btnstr:""
      }
    },
    props:['database'],
    computed:{
      newStyle(num){
        if(this.newConHeight === 80){
          this.btnstr = "更多 >>";
          return {
            "max-height": this.newConHeight + "px"
          }
        }else{
          this.btnstr = "缩小 <";
          return {
            "max-height": "inherit"
          }
        }
      }
    },
    methods:{
      showContent(){
        var num = this.newConHeight;
        this.newConHeight= num ===80 ? 200 : 80;
        console.log("newStyle::")
      }
    }
  }
</script>

<style lang="less">
.fix{clear:both;}
.newslist{
  margin-top:30px;
  clear:both;
  img {
    display:inline-block;
    width:211px;
    height:151px;
    float:left;
    background: #333;
  }
  .con{
    margin-left:221px;
    line-height:20px;
    font-size:12px;
    .tits{
      color:#f3f3f3;
    }
    .more{
      padding: 10px 0 10px 0;
      b{
        font-weight:normal;
        display:block;
        color:#fff;
        cursor:pointer;
      }
    }
    .text{
      line-height:25px;
      color:#9e9e9e;
      font-size: 12px;
      position: relative; 
      line-height: 20px; 
      // max-height: 80px;
      overflow: hidden;
      &:after{
        content: "\00A0"; position: absolute; bottom: 0; right:5px; padding-left: 22px;
        background: linear-gradient(to right, transparent, #000 55%);
      }
    }
    .time{
      color:#9e9e9e;
    }
  }
}


@media screen and (min-width: 320px) and (max-width: 768px) {

  .newslist{
    .con{
        margin:15px 0 0 0;
        .more{
            float:left;
        }
        .time{
            float:right;
            padding:10px 0 0 0;
        }
    }
    img{
        float:none;
    }
  }

}


</style>


