<template>
  <layout :active="2">
    <div class="newscontent">
      <div class="company" v-for="record in newsdata.company">
        <div class="title" v-html="record.title"></div>
        <p v-html="record.content"></p>
      </div>
      <div v-if="newsdata.news.length>0" class="title"><i>新聞</i></div>
      <list v-if="newsdata.news.length>0" v-for="record in newsdata.news" :database="record"></list>
    </div>
  </layout>
</template>

<script>
  import Layout from '../modules/layout.vue'
  import List from './list.vue'
  import {newsdata} from '../mock-data/newsdata.js'
  export default {
    components:{
      Layout,
      List
    },
    data(){
      return{
        newsdata:newsdata
      }
    }
  }

</script>

<style lang="less">
.newscontent{
  padding:50px 0;
  color:#fff;
  font-size:14px;
  color:#fdfdfd;
  // line-height:50px;
  .title{
    padding-top: 40px;
    i{
      border-bottom:2px solid #ffffff;
      display:inline-block;
      padding:0 0 20px 0;
      font-style: initial;
    }
  }
  p{
    padding-top:20px;
    width: 587px;
    color:#fff;
    line-height:26px;
    font-size:13px;
  }
}

@media screen and (min-width: 320px) and (max-width: 768px) {
  .newscontent{
      padding:30px;
  }

}



</style>


